html, body, #root {
  height: 100%;
  background-color: #343d52;
}

.App {
  text-align: center;
}

.Header{
  height: 180px;
  background-color: #0c3e85;
  overflow: scroll;
  position: relative;
}

.Name {
  margin: 10px;
  font: 400 90px/1.3 'Oleo Script', Helvetica, sans-serif;
  color: #2b2b2b;
  text-shadow: 4px 4px 0px rgba(0,0,0,0.1);
}

.HeaderLinks {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 20px;
}

.Header .Link {
  display: inline-block;
  margin-left: 100px;
  margin-right: 100px;
}

.Header .Link a {
  color: inherit;
  text-decoration: none;
  padding: 10px;
  padding-top: 7px;
  padding-bottom: 7px;
  border: solid gray 1px;
  border-radius: 20%;
  background-color: lightgray;
}

.Projects .Link {
  margin-bottom: 100px;
}

.Projects .Link a {
  color: inherit;
  text-decoration: none;
  width: 200px;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  border: solid gray 1px;
  border-radius: 20%;
  background-color: darkgray;
}

.PageTitle {
  margin: 30px;
  margin-bottom: 70px;
  font: 400 60px/1.3 'Oleo Script', Helvetica, sans-serif;
  color: #000000;
  text-shadow: 4px 4px 0px rgba(0,0,0,0.1);
}

.AboutMe {
  width: 60%;
  left: 20%;
  position: relative;
}

.AboutDescription {
  float: left;
  width: 60%;
  font-size: large;
}

.AboutPicture {
  float: right;
}

.Snek table.center {
  margin-left: auto; 
  margin-right: auto;
}

.Snek {
  margin-bottom: 15px;
}